import { ReactNode, useEffect, useState } from 'react';

import Typography from 'components/ui/Typography';

import classNames from 'classnames';

type Item = {
  key: number;
  label: ReactNode;
  value: string;
};

export interface PickerProps {
  variant?: 'button' | 'example';
  shouldMultiCheck?: boolean;
  list: Item[];
  initialSelect?: number[];
  onUpdateSelected?: (selected: number[]) => void;
}

const VARIANT_MAPS = {
  button: '',
  example: ''
};

const Picker = ({
  variant = 'button',
  shouldMultiCheck = false,
  list,
  initialSelect,
  onUpdateSelected
}: PickerProps) => {
  const [select, setSelect] = useState<number[]>(initialSelect ?? []);

  const onHandleSelect = (key: number) => {
    if (shouldMultiCheck) {
      setSelect((prev) => {
        if (prev.includes(key)) {
          return prev.filter((item) => item !== key);
        }
        return [...prev, key];
      });
    } else {
      setSelect([key]);
    }
  };

  useEffect(() => {
    if (onUpdateSelected) {
      onUpdateSelected(select);
    }
  }, [onUpdateSelected, select]);

  return (
    <ul className="flex flex-col items-center grow gap-5">
      {list?.map(({ key, label }) => (
        <li key={key} className="flex w-full">
          <button
            type="button"
            className={classNames(
              'inline-flex items-center justify-center grow min-h-[4rem] py-2 px-4 rounded-button-xl focus:[:not(:focus-visible):outline-none] focus-visible:outline-1 focus-visible:-outline-offset-1 focus-visible:outline-primary-500',
              VARIANT_MAPS[variant],
              select.includes(key) ? 'bg-gray-800' : 'bg-gray-300'
            )}
            onClick={() => onHandleSelect(key)}
          >
            <Typography
              align="center"
              color={select.includes(key) ? 'text-white' : 'text-gray-700'}
            >
              {label}
            </Typography>
          </button>
        </li>
      ))}
    </ul>
  );
};

export default Picker;
